.projects {
    padding: 2rem;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 1rem;
  }
  
  .project-card {
    background-color: #f7f7f7;
    padding: 1rem;
    border-radius: 4px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  }
  
  .project-card h3 {
    font-size: 24px;
    margin-bottom: 0.5rem;
  }
  
  .project-card p {
    margin-bottom: 1rem;
  }

  .projects-container {
    font-family: 'Montserrat', sans-serif;
    font-weight: 500;
    text-align: center;
  }
  
  .projects-title {
    margin-top: 2.5rem;
    font-family: 'Montserrat', sans-serif;
    font-weight: 500;
    font-size: 30px;
    margin-bottom: 0rem;
  }
  
  .projects a {
    color: var(--secondary-color); /* Use your preferred color */
    text-decoration: none;
  }
  
  .projects a:hover {
    text-decoration: underline;
  }
  